body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  background-color: #333;
  color: white;
  padding: 10px 0;
  position: sticky;
  top: 0;
  width: 100%;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.nav-links {
  list-style: none;
  display: flex;
}

.nav-links li {
  margin-left: 20px;
}

.nav-links a {
  text-decoration: none;
  color: white;
  padding: 5px 10px;
}

.nav-links a:hover {
  background-color: #575757;
  border-radius: 5px;
}

.menu-icon {
  display: none;
  cursor: pointer;
}

@media (max-width: 768px) {
  .nav-links {
      display: none;
      flex-direction: column;
      width: 100%;
  }
  .nav-links li {
      margin: 0;
      text-align: center;
  }
  .menu-icon {
      display: block;
  }
}

.center {
  text-align: center;
}

ul {
  list-style-type: none;
}

body {
  margin: 10px;
  padding: 20px;
}
img {
  max-width: 100%;
  height: auto;
  width: auto\9; /* ie8 */
}

